import { trigger } from "@utils/utils"
import { tick } from "svelte"

declare let __webpack_public_path__: string

if (typeof __webpack_public_path__ !== "undefined") {
  __webpack_public_path__ = (window as any).dp_public_path
}

async function loadVariables () {
  const res = await $.ajax({
    url     : window.dp.controllers.loader,
    method  : "post",
    dataType: "json",
    data    : {
      action: "load_variables",
      ...window.dp,
    },
  })
  if (res.success) {
    Object.keys(res.variables).forEach(key => {
      window[key] = res.variables[key]
    })
    loadApp()
  }
}

async function loadApp () {
  await Promise.all([
    import("@views/scss/front/product-buttons.scss"),
    import("@views/ts/product"),
  ])
  trigger(document, "dp-loaded")
  await tick()
  window.dp.loaded = true
}

$(window).on("load", () => {
  loadVariables()
})
